import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ManageaccountsService {

  constructor(private http: Http) { }

  getAllAccounts() {
    return this.http.get('/account')
      .map(res => res.json());
  }

}



