import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;

  constructor(
    private validateService: ValidateService,
    private flashMessage:FlashMessagesService,
    private authService:AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    console.log('ta vindo aqui?');
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      kind: 'Employee'
    };

    console.log(user);

    // Required Fields
    if (user.name === undefined || user.email === undefined ||
    user.username === undefined || user.password === undefined) {
      console.log('vindo aquii:?');
      this.flashMessage.show('Por favor preencha todos os campos', {cssClass: 'alert-warning', timeout: 3000});
      return false;
    }


    console.log(user);
    // Register user
    this.authService.registerUser(user).subscribe(data => {
      console.log('aqui?');
      if (data.success) {
        this.flashMessage.show('Registrado com sucesso!', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Falha ao registrar, usuario ou e-mail ja existem', {cssClass: 'alert-warning', timeout: 3000});
        this.router.navigate(['/register']);
      }
    });

  }

}



