import { Component, OnInit } from '@angular/core';
import { MdDialogRef } from '@angular/material';
import { ManageitensService } from '../../services/manageitens.service';
import {FlashMessagesService} from 'angular2-flash-messages';

@Component({
  selector: 'app-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit {

  item = {};
  categories: any[];
  selectedCategory: any;
  selectedCategoryType: any;

  constructor(public dialogRef: MdDialogRef<ItemComponent>,
              public manageItensService: ManageitensService,
              public flashMessage: FlashMessagesService) { }

  ngOnInit() {
    this.selectedCategory = {};

    this.manageItensService.getAllCategories()
      .subscribe((res) => {
        console.log(res);
        this.categories = res;
        console.log(this.categories);

      });
  }

  onClose() {
    this.dialogRef.close();
  }

  saveItem(item) {
    if (item.name === undefined || item.description === undefined ||
        this.selectedCategory === undefined || this.selectedCategoryType === undefined ||
        item.price === undefined) {
          console.log('vindo aquii:?');
          alert('Por favor preencha os campos obrigatorios');
          return false;
        }

    item.category = this.selectedCategory.category;
    item.categorytype = this.selectedCategoryType;
    console.log(item);

    if (item._id) {
      console.log('editar');
      this.manageItensService.updateItem(item)
        .subscribe((res) => {
          console.log(res);
          this.flashMessage.show('Item editado com sucesso.', { cssClass: 'alert-success', timeout: 5000 });
        });
    } else {
      console.log('criar');
      this.manageItensService.createItem(item)
        .subscribe((res) => {
          console.log(res);
          this.flashMessage.show('Item criado com sucesso.', { cssClass: 'alert-success', timeout: 5000 });
        });
    }

    this.dialogRef.close();

  }

}



